.navbar-brand {
  color: #ff6333;
  -webkit-animation: pulse 4s linear;
          animation: pulse 4s linear;
  font-size: 28px;
  font-family: "Special Elite", "Helvetica";
}

header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 10;
}

.portfolio {
    background-color: #AAC0D5;
    max-width: 1200px;
    border: 3px solid #2D3439;
    border-radius: 20px;
    margin-bottom: 60px;
}

.portfolio h1 {
    text-align: center;
    padding: 10px;
}


.project-image {
  display: block;
  max-width: 800px;
  margin: 0 auto;
  border: 2px solid #2d3439;
  border-radius: 10px;
}

@media (max-width: 860px) {
  .project-window {
    height: 400rem;
    overflow: scroll;
  }
  .project-image {
    object-fit: cover;
    width: 100%;
  }
}

